body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.layout{
  width: 85%;
  margin: 0 auto;
}

.header{
  display: flex;
  height: 132px;
  justify-content: center;
  align-items: center;
  list-style: none;
  border-bottom: 3px solid black;
  margin-bottom: 10px;
}

.header .logo{
  width: 90%;
  height: auto;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: start;
  line-height: 1;
}

.header .logo span{
  display: block;
  font-size: 60px;
  font-weight: bolder;
  color: red;
  margin-top: 25px;
  margin-bottom: 0px;
}

.header .logo h3{
  font-size: 15px;
  display: block;
  flex-basis: 20px;
  margin-top: 2px;
  margin-left: 40px;
  font-weight: 300px;
}

.header nav{
  display: block;
  margin-top: 50px;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  flex-grow: 1;
}

.header nav ul{
  height: 132px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}

.header li{
  text-transform: uppercase;
  font-weight: bold;
  font-size: 17px;
  margin-left: 15px;
  margin-right: 15px;
}

.header li a{
  color: black;
  text-decoration: none;
  transition: all 300ms;
}

.header .active,
.header a:hover{
  color: red;
}

.header button{
  position:relative;
  margin-top: 95px;
  padding: 5px;
}

.header .active{
  display: block;
}

.header .inactive{
  display: none;
}

.menu-lateral{
  position: absolute;
  top: 0;
  left: 10;
  width: 250px;
  height: 100vh;
  background-color: red;
  color: #fff;
  padding: 20px;
  transform: translateX(-30%);
  transition: transform 0.3s ease-in-out;
}

.menu-lateral ul{
  list-style: none;
  justify-content: flex-start;
}

.menu-lateral li a{
  color: white;
  text-decoration: none;
  transition: all 300ms;
}

.menu-lateral .active-menu,
.menu-lateral a:hover{
  color: black;
}

.menu-lateral span,
.menu-lateral label{
  font-weight: bold;
  font-size: 20px;
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: 10px;
  margin-left: 24px;
  margin-bottom: 0px;
}

.content{
  min-height: 400px;
}

.div-foto{
  display: flex;
  justify-content: center;
  text-align: center;
  margin: 10px;
}

.foto{
  height: auto;
  width: 100%;
  border-radius: 12px;
}

.portada-img{
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.content-portada{
  width: 60%;
  margin-left: 10px;
  margin-top: 0px;
  text-align: justify;
}

.home h1{
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-weight: 400;
  font-size: 45px;
  margin-bottom: 0px;
}

.home strong{
  color: red;
}

.home h2{
  color: rgb(96, 96, 96);
  font-weight: lighter;
}

.home .title a{
  padding: 5px;
  border-radius: 5px;
  color: white;
  background-color: red;
  text-decoration: none;
  transition: all 300ms;
}

.page .heading,
.home .heading{
  font-size: 40px;
  font-weight: 500;
  color: black;
  margin-bottom: 0px;
}

.page .heading{
  font-weight: bold;
}

input[type="text"],
input[type="number"],
textarea{
  display: block;
  padding: 15px;
  margin-top: 10px;
  width: 400px;
  border-radius: 4px;
  border: solid 1px black;
  transition:  700ms;
  outline: none;
}

input:focus,
textarea:focus {
  border: solid 1px red; /* cambia el color del borde a azul */
  box-shadow: 0 0 5px rgba(0, 105, 143, 0.5); /* agrega un efecto de sombra */
}

.datos-contact{
  justify-content: flex-end;
  margin-right: 10px;
  margin-top: 20px;
  font-size: large;
  font-weight: bold;
}

.page .contact{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-grow: 2;
}

.page .contact-form{
  margin-right: 0px;
  width: 100%;
  text-align: center;
}

.page .contact-form input,
.page .contact-form textarea{
  width: auto;
  min-width: 300px;
}

.page .contact-form [type="submit"]{
  width: auto;
  min-width: 330px;
}

.page .contact hr{
  margin: 10px;
}

.page h2:hover{
  color: red;
}

.last-works{
  justify-content: center;
}

.last-works hr{
  width: 30%;
  margin-top: 30px;
  border-color: rgb(248, 200, 200);
}

.last-works .heading{
  margin-top: 30px;
}

input[type="submit"],
button{
  font-size: 16px;
  padding: 10px;
  margin-top: 10px;
  border: 0px;
  border-radius: 5px;
  color: white;
  background-color: red;
  text-decoration: none;
  transition: all 300ms;
  cursor: pointer;
}

.home .title a:hover,
input[type="submit"]:hover,
button:hover{
  background-color: rgb(251, 86, 86);
}

.works{
  display: flex;
  flex-wrap: wrap;
}

.work-item{
  flex-basis: 30%;
  flex-grow: 1;
  margin: 30px;
}

.work-item .mask{
  height: auto;
  justify-content: center;
  overflow: hidden;
  border: 1px solid #ddd; 
  box-shadow: 0px 5px 14px #ddd;
}

.mask img{
  width: 100%;
  max-height: 250px;
  margin-bottom: 2px;
  transition: all 700ms;
}

.mask img:hover{
  transform: scale(1.1);
  opacity: 0.8;
}

.work-item span{
  color: gray;
  margin-top: 5px;
  display: block;
}

.work-item h2{
  color: black;
  font-size: 30px;
  margin-top: 5px;
  margin-bottom: 0px;
  display: block;
}

.work-item h3{
  margin-top: 0px;
  display: block;
  color: gray;
  padding-top: 5px;
  margin-top: 0px;
  border-top: 1px solid #ddd;
}

.work-item a{
  color: black;
  text-decoration: none;
  transition: all 300ms;
}

.work-item a:hover{
  color: red;
}

.page-work{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  
}

.page-work .mask{
  width: 50%;
  height: auto;
  overflow: hidden;
  box-shadow: 0px 0px 30px #ccc;
  border-radius: 5px;
}

.page-work .mask img{
  width: 100%;
}

.icon{
  color: red;
}

.contact svg:hover{
  color: rgb(251, 86, 86);
}

.incons-redes{
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: 5px;
  padding: 2px;
}

.icon-red{
  color: red;
  margin: 3px;
  height: 30px;
}

.content-logo{
  display: flex;
  flex-wrap: wrap;
  margin: 5px;
}

.content-logo img{
  width: 40px;
  height: 40px;
  margin-right: 5px;
  background: white;
  transition: all 300ms;
}

.content-logo img:hover{
  transform: scale(1.1);
  opacity: 0.8;
}

.footer{
  min-height: 40px;
  line-height: 15px;
  text-align: center;
  font-size: 14px;
  margin-top: 50px;
  padding-top: 45px;
  border-top: 3px solid black;
  background: #f5f5f5;
  color: #444;
}

@media (max-width: 768px) {
  /* Estilos para pantallas pequeñas */

  .header nav{
    display: none;
  }

  .header .boton-menu{
    display: block;
  }

  .header .logo span{
    font-size: 55px;
  }

  .content-portada{
    width: 100%;
  }

  .header .logo h3{
    font-size: 12px;
  }

  .home h1{
    font-size: 30px;
  }
  
  .header .logo{
    text-align: center;
  }

  .header .logo h3{
    margin-left: 10px;
  }

  /* Ajusta el tamaño de fuente y espaciado */
  body {
    font-size: 14px;
    line-height: 1.5;
  }
  
  /* Oculta o reduce elementos que no son esenciales */
  .sidebar {
    display: none;
  }
  
  /* Ajusta el ancho de los contenedores */
  .container {
    width: 90%;
    margin: 0 auto;
  }
  
  /* Ajusta el tamaño de las imágenes */
  img {
    width: 100%;
    height: auto;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  /* Estilos para pantallas medianas */
  .header nav{
    display: block;
  }

  .header .boton-menu{
    display: none;
  }
  
  /* Ajusta el tamaño de fuente y espaciado */
  body {
    font-size: 16px;
    line-height: 1.6;
  }
  
  /* Muestra elementos que estaban ocultos en pantallas pequeñas */
  .sidebar {
    display: block;
    width: 25%;
  }
  
  /* Ajusta el ancho de los contenedores */
  .container {
    width: 80%;
    margin: 0 auto;
  }
  
  /* Ajusta el tamaño de las imágenes */
  img {
    width: 50%;
    height: auto;
  }
}

@media (min-width: 1025px) {
  /* Estilos para pantallas grandes */
  .header nav{
    display: block;
  }

  .header .boton-menu{
    display: none;
  }
  
  /* Ajusta el tamaño de fuente y espaciado */
  body {
    font-size: 18px;
    line-height: 1.7;
  }
  
  /* Ajusta el ancho de los contenedores */
  .container {
    width: 70%;
    margin: 0 auto;
  }
  
  /* Ajusta el tamaño de las imágenes */
  img {
    width: 33%;
    height: auto;
  }
}
